<template>
  <MyFrame>
    <el-card>
      <div slot="header">
        <span>OLAP-</span>
      </div>
      <el-row>
        <el-col span=5>
          <el-cascader
            v-model="selectedMetaData"
            :options="metaDataTree"
            :props="{ 'checkStrictly': 'true' }"
            expandTrigger="hover"
            @change="handleChange" clearable="true">
            <!--            placeholder="" clearable-->
          </el-cascader>
        </el-col>
        <el-col span=10 push=3>
          <el-date-picker
            v-model="date"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </el-col>
        <el-col span=3 push=4>
          <el-button type="primary" v-on:click="newSearchClicked"></el-button>
        </el-col>
        <el-col span=3 push=3>
          <el-button type="primary"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <el-row>
      <el-col span=24>
        <el-table id="showTable"
                  :data="tableData"
                  border
                  stripe
                  height="300"
                  max-height="300"
        >
          <!--              max-height="400px"-->
          <el-table-column
            type="index"
            width="50">
          </el-table-column>
          <el-table-column
            prop="date"
            label=""
          >
          </el-table-column>
          <el-table-column
            prop="measurePoint"
            label=""
          >
          </el-table-column>
          <!--              <el-table-column-->
          <!--                prop="location"-->
          <!--                label=""-->
          <!--                width="180">-->
          <!--              </el-table-column>-->
          <el-table-column
            prop="value"
            label=""
          >
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col span=5>
        <el-card style="height: 400px;">
          <div slot="header">
            <span></span>
          </div>
          <el-row style="margin-top: 10px">
            <el-select value="" v-model="measurePoint1" placeholder="1">
              <el-option value="Ia"></el-option>
              <el-option value="Ib"></el-option>
              <el-option value="Ic"></el-option>
              <el-option value="A"></el-option>
              <el-option value="C"></el-option>
              <el-option value="AB"></el-option>
              <el-option value="BC"></el-option>
              <el-option value="CA"></el-option>
              <el-option value=""></el-option>
              <el-option value=""></el-option>
              <el-option value=""></el-option>
              <el-option value=""></el-option>
            </el-select>
          </el-row>
          <el-row style="margin-top: 10px">
            <el-select value="" v-model="measurePoint2" placeholder="2">
              <el-option value="Ia"></el-option>
              <el-option value="Ib"></el-option>
              <el-option value="Ic"></el-option>
              <el-option value="A"></el-option>
              <el-option value="C"></el-option>
              <el-option value="AB"></el-option>
              <el-option value="BC"></el-option>
              <el-option value="CA"></el-option>
              <el-option value=""></el-option>
              <el-option value=""></el-option>
              <el-option value=""></el-option>
              <el-option value=""></el-option>
            </el-select>
          </el-row>
          <el-row style="margin-top: 100px;">
            <el-button type="primary" v-on:click="exportExcel"></el-button>
            <el-button type="primary" v-on:click="generateChart"></el-button>
          </el-row>
          <el-row>
            <el-button type="primary" v-on:click="clearChart"></el-button>

            <el-button type="primary" v-on:click="compareChart"></el-button>

          </el-row>
        </el-card>
      </el-col>
      <el-col span=18 push=1>
        <div id="chart" style="height: 400px ;"></div>
      </el-col>
    </el-row>
  </MyFrame>
</template>

<script>
    import * as echarts from 'echarts';
    import Navmenu from "../components/NavMenu";
    import Sidebar from "../components/Sidebar"
    import axios from 'axios'
    import FileSaver from 'file-saver'
    import XLSX from 'xlsx'
    import MyFrame from "../components/Frame";

    function fixDateFormat(oriDates) {
        let newDates = [];
        for (let oriDate of oriDates) {
            let tempDate = oriDate.slice(0, 4) + "-" + oriDate.slice(4, 6) + "-" + oriDate.slice(6, 8);
            newDates.push(tempDate)
        }
        return oriDates

    }

    export default {
        name: "olap2",
        components: {MyFrame, Sidebar, Navmenu},
        data() {
            return {
                date: '',
                location: '',
                factory: '',
                line: '',
                device: '',
                measurePoint: '',
                measurePoint1: '',
                measurePoint2: '',
                idate: '',
                tableData: '',
                chartRef: '',
                metaDataTree: '',
                selectedMetaData: '',
                options: [{
                    value: 'zhinan',
                    label: '',
                    children: [{
                        value: 'shejiyuanze',
                        label: '',
                        children: [{
                            value: 'yizhi',
                            label: ''
                        }, {
                            value: 'fankui',
                            label: ''
                        }, {
                            value: 'xiaolv',
                            label: ''
                        }, {
                            value: 'kekong',
                            label: ''
                        }]
                    }, {
                        value: 'daohang',
                        label: '',
                        children: [{
                            value: 'cexiangdaohang',
                            label: ''
                        }, {
                            value: 'dingbudaohang',
                            label: ''
                        }]
                    }]
                }, {
                    value: 'zujian',
                    label: '',
                    children: [{
                        value: 'basic',
                        label: 'Basic',
                        children: [{
                            value: 'layout',
                            label: 'Layout '
                        }, {
                            value: 'color',
                            label: 'Color '
                        }, {
                            value: 'typography',
                            label: 'Typography '
                        }, {
                            value: 'icon',
                            label: 'Icon '
                        }, {
                            value: 'button',
                            label: 'Button '
                        }]
                    }, {
                        value: 'form',
                        label: 'Form',
                        children: [{
                            value: 'radio',
                            label: 'Radio '
                        }, {
                            value: 'checkbox',
                            label: 'Checkbox '
                        }, {
                            value: 'input',
                            label: 'Input '
                        }, {
                            value: 'input-number',
                            label: 'InputNumber '
                        }, {
                            value: 'select',
                            label: 'Select '
                        }, {
                            value: 'cascader',
                            label: 'Cascader '
                        }, {
                            value: 'switch',
                            label: 'Switch '
                        }, {
                            value: 'slider',
                            label: 'Slider '
                        }, {
                            value: 'time-picker',
                            label: 'TimePicker '
                        }, {
                            value: 'date-picker',
                            label: 'DatePicker '
                        }, {
                            value: 'datetime-picker',
                            label: 'DateTimePicker '
                        }, {
                            value: 'upload',
                            label: 'Upload '
                        }, {
                            value: 'rate',
                            label: 'Rate '
                        }, {
                            value: 'form',
                            label: 'Form '
                        }]
                    }, {
                        value: 'data',
                        label: 'Data',
                        children: [{
                            value: 'table',
                            label: 'Table '
                        }, {
                            value: 'tag',
                            label: 'Tag '
                        }, {
                            value: 'progress',
                            label: 'Progress '
                        }, {
                            value: 'tree',
                            label: 'Tree '
                        }, {
                            value: 'pagination',
                            label: 'Pagination '
                        }, {
                            value: 'badge',
                            label: 'Badge '
                        }]
                    }, {
                        value: 'notice',
                        label: 'Notice',
                        children: [{
                            value: 'alert',
                            label: 'Alert '
                        }, {
                            value: 'loading',
                            label: 'Loading '
                        }, {
                            value: 'message',
                            label: 'Message '
                        }, {
                            value: 'message-box',
                            label: 'MessageBox '
                        }, {
                            value: 'notification',
                            label: 'Notification '
                        }]
                    }, {
                        value: 'navigation',
                        label: 'Navigation',
                        children: [{
                            value: 'menu',
                            label: 'NavMenu '
                        }, {
                            value: 'tabs',
                            label: 'Tabs '
                        }, {
                            value: 'breadcrumb',
                            label: 'Breadcrumb '
                        }, {
                            value: 'dropdown',
                            label: 'Dropdown '
                        }, {
                            value: 'steps',
                            label: 'Steps '
                        }]
                    }, {
                        value: 'others',
                        label: 'Others',
                        children: [{
                            value: 'dialog',
                            label: 'Dialog '
                        }, {
                            value: 'tooltip',
                            label: 'Tooltip '
                        }, {
                            value: 'popover',
                            label: 'Popover '
                        }, {
                            value: 'card',
                            label: 'Card '
                        }, {
                            value: 'carousel',
                            label: 'Carousel '
                        }, {
                            value: 'collapse',
                            label: 'Collapse '
                        }]
                    }]
                }, {
                    value: 'ziyuan',
                    label: '',
                    children: [{
                        value: 'axure',
                        label: 'Axure Components'
                    }, {
                        value: 'sketch',
                        label: 'Sketch Templates'
                    }, {
                        value: 'jiaohu',
                        label: ''
                    }]
                }]
            };
        }
        ,
        methods: {
            exportExcel: function () {
                /* generate workbook object from table */
                var wb = XLSX.utils.table_to_book(document.getElementById('showTable'))
                /* get binary string as output */
                var wbout = XLSX.write(wb, {bookType: 'xlsx', bookSST: true, type: 'array'})
                try {
                    FileSaver.saveAs(new Blob([wbout], {type: 'application/octet-stream'}), '.xlsx')
                } catch (e) {
                    if (typeof console !== 'undefined') console.log(e, wbout)
                }
                return wbout
            }
            ,
            fixDateFormat: function (oriDate) {
                return oriDate.slice(0, 4) + "-" + oriDate.slice(4, 6) + "-" + oriDate.slice(6, 8)
            }
            ,
            getValueByMeasurePoint: function (imeasure) {
                let allValue = [];
                for (let i of this.tableData) {
                    if (i.measurePoint === imeasure) {
                        allValue.push(i.value)
                    }
                }
                return allValue
            }
            ,
            getAllDate: function () {
                let allDate = new Set();
                for (let i of this.tableData) {
                    allDate.add(i.date)
                }
                return Array.from(allDate)
            }
            ,
            searchClicked: function () {
                let that = this;
                axios.post('/api/getSpecificData',
                    {
                        factory: that.factory,
                        line: that.line,
                        device: that.device,
                        timestamp: that.date

                    }).then(function (response) {
                    that.tableData = response.data;
                })
            }
            ,
            generateChart: function () {
                let date = this.getAllDate();
                let value = this.getValueByMeasurePoint(this.measurePoint1);
                let option = {
                    title: {
                        text: ''
                        // subtext: ''
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {},
                    toolbox: {
                        show: true,
                        feature: {
                            dataZoom: {
                                yAxisIndex: 'none'
                            },
                            dataView: {readOnly: false},
                            magicType: {type: ['line', 'bar']},
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    xAxis: {
                        type: 'category',
                        data: fixDateFormat(this.getAllDate())
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: value,
                        type: 'line',
                        name: this.measurePoint1
                    }],
                };
                let chart = echarts.init(document.getElementById("chart"));
                chart.setOption(option, true)
            }
            ,
            compareChart: function () {
                let date = this.getAllDate();
                let value1 = this.getValueByMeasurePoint(this.measurePoint1);
                let value2 = this.getValueByMeasurePoint(this.measurePoint2);
                let option = {
                    title: {
                        text: '',
                        // subtext: ''
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        // data: ['1', '2']
                    },
                    toolbox: {
                        show: true,
                        feature: {
                            dataZoom: {
                                yAxisIndex: 'none'
                            },
                            dataView: {readOnly: false},
                            magicType: {type: ['line', 'bar']},
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    xAxis: {
                        type: 'category',
                        data: fixDateFormat(this.getAllDate())
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        {
                            data: value1,
                            type: 'line',
                            name: this.measurePoint1
                        },
                        {
                            data: value2,
                            type: 'line',
                            name: this.measurePoint2
                        }
                    ]
                };
                let chart = echarts.init(document.getElementById("chart"));
                chart.setOption(option, true)
            }
            ,
            clearChart: function () {
                this.measurePoint1 = '';
                this.measurePoint2 = '';
                var chart = echarts.init(document.getElementById("chart"));
                this.$chart = chart;
                var option = {
                    title: {
                        text: '',
                        subtext: ''
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data: ['1', '2']
                    },
                    toolbox: {
                        show: true,
                        feature: {
                            dataZoom: {
                                yAxisIndex: 'none'
                            },
                            dataView: {readOnly: false},
                            magicType: {type: ['line', 'bar']},
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['', '', '', '', '', '', '']
                    },
                    yAxis: {
                        type: 'value',
                        axisLabel: {
                            formatter: '{value}'
                        }
                    },
                    series: [
                        {
                            name: '1',
                            type: 'line',
                            data: [11, 11, 15, 13, 12, 13, 10],
                            markPoint: {
                                data: [
                                    {type: 'max', name: ''},
                                    {type: 'min', name: ''}
                                ]
                            },
                            markLine: {
                                data: [
                                    {type: 'average', name: ''}
                                ]
                            }
                        },
                        {
                            name: '2',
                            type: 'line',
                            data: [1, -2, 2, 5, 3, 2, 0],
                            markPoint: {
                                data: [
                                    {name: '', value: -2, xAxis: 1, yAxis: -1.5}
                                ]
                            },
                            markLine: {
                                data: [
                                    {type: 'average', name: ''},
                                    [{
                                        symbol: 'none',
                                        x: '90%',
                                        yAxis: 'max'
                                    }, {
                                        symbol: 'circle',
                                        label: {
                                            normal: {
                                                position: 'start',
                                                formatter: ''
                                            }
                                        },
                                        type: 'max',
                                        name: ''
                                    }]
                                ]
                            }
                        }
                    ]
                };

                chart.setOption(option, true)
            }
            ,
            getMetaData: function () {
                let that = this;
                axios.post("/api/getMetaDataTree").then(function (response) {
                    that.metaDataTree = response.data
                });
            }
            ,
            handleChange: function () {
                this.factory = this.selectedMetaData[0];
                this.line = this.selectedMetaData[1];
                this.device = this.selectedMetaData[2];
            }
            ,
            newSearchClicked: function () {
                let that = this;
                axios.post('/api/getSpecificData',
                    {
                        factory: this.factory,
                        line: this.line,
                        device: this.device,
                        timestamp: this.date
                    }).then(function (response) {
                    that.tableData = response.data
                })
            }
        }
        ,
        mounted() {
            var chart = echarts.init(document.getElementById("chart"));
            var option = {
                title: {
                    text: '',
                    subtext: ''
                },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {
                    data: ['1', '2']
                },
                toolbox: {
                    show: true,
                    feature: {
                        dataZoom: {
                            yAxisIndex: 'none'
                        },
                        dataView: {readOnly: false},
                        magicType: {type: ['line', 'bar']},
                        restore: {},
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: ['', '', '', '', '', '', '']
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value}'
                    }
                },
                series: [
                    {
                        name: '1',
                        type: 'line',
                        data: [11, 11, 15, 13, 12, 13, 10],
                        markPoint: {
                            data: [
                                {type: 'max', name: ''},
                                {type: 'min', name: ''}
                            ]
                        },
                        markLine: {
                            data: [
                                {type: 'average', name: ''}
                            ]
                        }
                    },
                    {
                        name: '2',
                        type: 'line',
                        data: [1, -2, 2, 5, 3, 2, 0],
                        markPoint: {
                            data: [
                                {name: '', value: -2, xAxis: 1, yAxis: -1.5}
                            ]
                        },
                        markLine: {
                            data: [
                                {type: 'average', name: ''},
                                [{
                                    symbol: 'none',
                                    x: '90%',
                                    yAxis: 'max'
                                }, {
                                    symbol: 'circle',
                                    label: {
                                        normal: {
                                            position: 'start',
                                            formatter: ''
                                        }
                                    },
                                    type: 'max',
                                    name: ''
                                }]
                            ]
                        }
                    }
                ]
            };

            chart.setOption(option, true)
            this.getMetaData()
        }
    }
</script>

<style scoped>
  .el-row {
    margin-bottom: 20px;
  }

  .el-card {
    margin-bottom: 20px;
  }
</style>



